body {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 1%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #263238;
}

#container {
  width: 98%;
  height: 57.89vw;
  max-width: 760px;
  max-height: 440px;
}

#input {
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 100%;
  height: 1px;
  border-radius: 8;
  background: #263238;
  outline: none;
  opacity: 1;
  cursor: pointer;
}

#input::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 0px;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  background: #263238;
  cursor: pointer;
}

#input::-webkit-slider-thumb:hover {
  background: #0CC2B5;
}

#input::-moz-range-thumb {
  -moz-appearance: none;
  border: 0px;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  background: #263238;
  cursor: pointer;
}

#input::-moz-range-thumb:hover {
  background: #0CC2B5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#maprf {
  position: relative;
  width: 100%;
  height: 100%;
  /* background-color: aquamarine; */

  display: grid;
  grid-template-columns: repeat(19, 1fr);
  grid-template-rows: repeat(11, 1fr);
}

#legend {
  position: relative;
  width: 100%;
  height: 8pt;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  margin-top: 21pt;
  margin-bottom: 36pt;
}

.legendTile {
  font-size: 8pt;
  position: relative;
  top: 12;
  transform: translate(0px,12px);
}

.regAbbr {
  font-size: 8pt;
  position: relative;
  top: 0;
  left: 0;
}

h1 {
  font-size: 24pt;
}

.tileCont {
  position: relative;
}

.tooltip[data-tool-tip] {
  position: relative;
  /* z-index: 10000; */
}
.tooltip[data-tool-tip]:hover::before {
  visibility: visible;
  opacity: 1;
}
.tooltip[data-tool-tip]::before {
  text-align:left;
  content: attr(data-tool-tip);
  visibility: hidden;
  opacity: 0;
  min-width: 100%;
  width: max-content;
  height: max-content;
  position: absolute;
  box-sizing: border-box;
  display: block;
  margin: 0 auto;
  padding: 4px 8px;
  background-color: #E2E2E6;
  color: #263238;
  border-radius: 4px;
  font-size: 10pt;
  /* font-weight: 500; */
  white-space: nowrap;
  transition: opacity ease-out 150ms, bottom ease-out 150ms;
}
.tooltip[data-position='top']::before {
  bottom: 100%;
}
.tooltip[data-position='right']::before {
  left: 100%;
  top: 0;
  bottom: 0;
  margin: auto 0;
}
.tooltip[data-position='bottom']::before {
  top: 100%;
}
.tooltip[data-position='left']::before {
  right: 100%;
  top: 0;
  bottom: 0;
  margin: auto 0;
  transform: translate(-12px,0px);
}

@media (max-width: 500px) {
  .regAbbr {
    display: none;
  }

  h1 {
    font-size: 18pt;
  }
}

@media (min-width: 700px) {
  .legendTile {
    font-size: 10pt;
    transform: translate(0px,16px);
  }
  .regAbbr {
    font-size: 10pt;
  }
}